import React from 'react';
import { withTranslation } from 'react-i18next';
import { Header } from '../../Common/Header/Header.tsx';
import './LocalShipping.scss';
import clubLogo from '../../../assets/imgs/club-logo.png'

const LocalShipping = props => {
  const { t } = props;
  
  return (
    <section className="local-shipping-section">
      <Header 
        title={t('LocalShipping.headerTitle')}
      />
      <div className="club-promotion">
        <img src={clubLogo} alt="embroidery club logo"/>
        <span>Con tu nmero de miembro recibe un 10% de descuento en tu pedido</span>
      </div>
      <div className="local-shipping-section__steps">
        <p>Este envo esta disponible si la direccin de entrega se encuentra dentro de las 16 alcaldas de la CDMX.</p>
        <p>Cada semana estar recibiendo pedidos los das jueves y viernes de 11 am a 6 pm (las entregas son los dias martes).</p>

        <h3>Pasos para realizar pedido:</h3>
        <ul className="steps-list">
          <li className="step">
            <p>Enviame una lista de lo que te gustara comprar
            por mensaje privado a instagram @speligro
            acompaado de tu cdigo postal para cotizar tu envo
            Ve a la pgina 4 para saber como redactar tu pedido.</p>
            <p>Mnimo de compra 150 pesos mexicanos. </p>
          </li>

          <li className="step">
            En respuesta a tu mensaje te confirmar
            si tengo todos los productos de tu lista,
            tambin te dire el costo de tu envo
            y te compartir los datos bancarios
            para que puedas hacer la transferencia o depsito.
          </li>
          <li className="step">
            Una vez hecho el pago necesito una foto
            o captura de pantalla de tu comprobante de pago
            y los siguientes datos:
            <ul>
              <li>Nombre de quien recibir el paquete</li>
              <li>Nmero celular</li>
              <li>Direccin de entrega</li>
            </ul>
          </li>
          <li className="step">
            <p>Si envas tu comprobante antes de las 6 pm del lunes,
            tu pedido ser enviado el martes de esa misma semana.</p>
            <p>El martes recibirs un mensaje a tu celular
            de Dostavista con la informacin de tu entrega,
            el horario de las entregas es de 12 a 7 pm.</p>
          </li>
        </ul>
      </div>
    </section>
  );
}       

export default  withTranslation('common') (LocalShipping);